import React, {Component} from "react";
import {Runtime, Inspector} from "@observablehq/notebook-runtime";
import notebook from "perception-illusion-part-i";

class Illusion extends Component {
  animationRef = React.createRef();

  componentDidMount() {
    Runtime.load(notebook, (cell) => {
      if (cell.name === "canvas") {
        return new Inspector(this.animationRef.current);
      }

    });
  }


  render() {
    return (
      <div className="Illusion">
        <div ref={this.animationRef} width="960" height="300"></div>
      </div>
    );
  }
}

export default Illusion;